.main {
  display: flex;
  padding-top: 120px;
  width: 1300px;
  gap: 60px;
}

.nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: start;
  padding-left: 20px;
}

.link_active {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-decoration: none;
  width: 320px;
  height: 64px;
  color: #f2f2f3;
}

.link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-decoration: none;
  width: 320px;
  height: 64px;
  color: #8585ad;
}

.link:hover {
  transition: all 0.6s linear;
  color: #f2f2f3;
}

.form {
  display: flex;
  flex-direction: column;
  width: 480px;
  justify-content: flex-start;
  align-items: center;
  gap: 24px;
}

.input {
  width: 100%;
}

.input div {
  width: 100%;
}

.input div div div:last-child {
  width: 24px;
  height: 24px;
}

.input svg {
  fill: #8585ad;
}
.input svg:hover {
  transition: all 0.6s linear;
  fill: white;
}

.activeInput svg {
  transition: all 0.6s linear;
  fill: white;
}

.text {
  width: 320px;
  height: 64px;
  padding-top: 80px;
}

.buttons {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}

.sectionSize {
  width: 840px;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow: auto;
  max-height: calc(100vh - 330px);
  position: relative;
}

.container::-webkit-scrollbar {
  width: 8px; /* ширина для вертикального скролла */
  background-color: #2f2f37;
}

.container::-webkit-scrollbar-thumb {
  height: 392px;
  background-color: #8585ad;
}
